@media (min-width: 768px) and (max-width: 990px) {
    .client-list div img {
        width: 130px;
    }
    .client-list div.col-sm-6:nth-child(3),
    .client-list div.col-sm-6:nth-child(4) {
        border-top-color: #f0f0f0;
    }
    .client-list div.col-sm-6:nth-of-type(2n+1) {
        border-left-color: transparent;
    }
}

@media (max-width: 767px) {
    .footer-links {
        margin-left: -20px;
        margin-right: -20px;
    }
    .footer-links a {
        padding: 10px 0 10px 45px;
    }
    .footer-links a span.icon {
        left: 20px;
    }
    .footer-links .row>div:nth-child(2) a {
        padding-left: 0;
        padding-right: 50px;
    }
    .footer-links .row>div:nth-child(2) a span.icon {
        left: auto;
        right: 20px;
    }
    .footer-links.margin-top-40 {
        margin-top: 20px;
    }
    #toast-container.toast-top-right {
        right: 20px;
    }
    #toast-container.toast-bottom-right {
        right: 20px;
    }
    #toast-container.toast-bottom-left {
        left: 20px;
    }
    #toast-container.toast-top-left {
        left: 20px;
    }
    .search-results.search-file .search-list li .file-icon {
        display: none;
    }
    .search-results.search-file .search-list li .search-result {
        width: 100%;
    }
    .client-list div img {
        width: 100px;
    }
    .client-list div.col-xs-6:nth-child(3),
    .client-list div.col-xs-6:nth-child(4) {
        border-top-color: #f0f0f0;
    }
    .client-list div.col-xs-6:nth-of-type(2n+1) {
        border-left-color: transparent;
    }
    .hide-on-mobile {
        display: none!important;
    }
}